import $ from 'jquery'; window.jQuery = $; window.$ = $;
import focusVisilbe from 'focus-visible';
import Inputmask from "inputmask";
import magnificPopup from 'magnific-popup';
// import slick from 'slick-carousel';
// import Swiper from 'swiper';

window.addEventListener("load", function () {
  
    /* Mobile menu */
    let mobileMenuToggle = document.querySelector('.hamburger');

    mobileMenuToggle.addEventListener('click', function(){
      
      let mobileMenu = document.querySelector('.mobile-menu');
  
      if ( mobileMenu.classList.contains('mobile-menu--active') ){
        this.classList.remove('hamburger--active'); 
        mobileMenu.classList.remove('mobile-menu--active');
      }
      else {
        this.classList.add('hamburger--active'); 
        mobileMenu.classList.add('mobile-menu--active');
      }
  
    });

    /* Popup */
    $('.btn-popup').magnificPopup({
      type: 'inline',
      fixedContentPos: false,
      closeOnBgClick: false,
      focus: '#callback-form-name'
    });

    /* Maskedinput */
    Inputmask("+7 (999) 999-99-99").mask(document.querySelectorAll(".form__input[type=tel]"));
    


    /* Input File */
    
    $('#price-form-file').change(function() {
      var input = $(this);
      var files = this.files;
      /**/
      if ( checkFiles(files) === false ){ //  -  
        input[0].value = ""; //    input
      }
    });
    
    function checkFiles(files) {
      var maxFileSize = 6291456; // 6 
      var status = 0;
      $(files).each(function(index, file) {
        if ( (file.size <= maxFileSize) && ((file.type == 'image/png') || (file.type == 'image/jpeg')) ) {
          // dropArea.find('.input-status').html('() ');
          console.log(' ');
          status = true;
        }
        else {
          // dropArea.find('.input-status').html(' ');
          console.log('  ');
          status = false;
        }
      });
      return status;
    }

    // E-mail Ajax Send
  $(".form").submit(function() {
    var th = $(this);
    var fd = new FormData(this);
    
    var btn = $('.form__send-btn'); //     
    
    btn.attr("disabled", true); //  
    btn.addClass('form__send-btn--loading'); //  
    
    $.ajax({
      type: "POST",
      url: "mail/send.php", //Path to send.php
      contentType: false,
      processData: false,
      data: fd
    }).done(function() {
      
      btn.removeClass('form__send-btn--loading'); //   
      
      $(th).find(".form__success").css('display', 'block').hide().fadeIn(); //    
      
      setTimeout(function() {
        $(th).find(".form__success").fadeOut(); //  4  
      }, 4000);
      
      setTimeout(function() { //  6   
        btn.attr("disabled", false); //  
        th.trigger("reset"); //  
        $.magnificPopup.close(); //  
      }, 6000);
    });
    return false;
  });

  
});


